<template>
<div class="all">
    <ul class="movie-list">
        <li @click="goDetail(movie.movieId)" v-for="movie in movieList" :key="movie.movieId" class="movie">
            <div class="movie-img"><img :src="movie.pic_url"></div>
            <div class="movie-info">
                <p>{{movie.movieName}}</p>
                <p>{{movie.genres}}</p>
                <p>评分:{{movie.xianqing.rating}}</p>
                <p>语言:{{movie.xianqing.language}}</p>
                <p>时间:{{movie.xianqing.runtime}}</p>
                <p>作者:{{movie.xianqing.writers}}</p>
            </div>
        </li>
        <div class="loading" v-show="loadingShow"><img src="@/assets/img/loading.gif"></div>
    </ul>
    <div class="tip" v-show="tip">
        <p>不能给的再多了</p>
    </div>
</div>
</template>

<script>
import Axios from "axios"
export default {
    data(){
        return{
            movieList:[],
            loadingShow:false,
            tip:false,
        }
    },
    methods:{
        loadingDate(){
            let url2 = '/static/data/moviedata1.json';
            Axios.get(url2)
            .then((res)=>{
                console.log(res);
                let limit = res.data.result;
                let data = limit.slice(
                    this.movieList.length,
                    this.movieList.length + 5
                );
                if(data.length<5){
                    this.tip = true;
                }
                this.movieList = this.movieList.concat(data);
            })
            .catch(()=>{
                alert("cuowu");
            })
        },
        goDetail(movieId){
            this.$router.push('/movie/moviedetail/'+movieId);
        }
    },
mounted () {
    this.loadingDate();
    window.onscroll = () =>{
    let clientHeight = document.documentElement.clientHeight;
    let scrollTop = Math.ceil(document.documentElement.scrollTop || document.body.scrollTop);
    let scrollHeight = document.documentElement.scrollHeight;
    //console.log(clientHeight,scrollTop,scrollHeight);
    
    if( scrollTop + clientHeight >= scrollHeight){
        this.loadingShow = true;
        //console.log("End!!!!");
        if(!this.tip){
            this.loadingDate();
        }else{
            this.loadingShow = false;
        }
        console.log('这里是世界尽头:'+this.tip,this.loadingShow)
            }
        }
    }
};
</script>

<style scoped>
.all{
    margin-bottom: 1rem;
}
.movie-list{
    margin-top: 1rem;
}
.movie{
    display: flex;
    padding: 0.2rem;
    border-bottom:1px solid #ccc;
}
.movie-img{
    margin-right: 0.2rem;
}
.movie-info{
    flex: 2 1 0;
}
.movie-info p:first-child{
    font-weight: bold;
    color: #000;
}
.loading{
    margin-bottom: 1rem;
    text-align: center;
}
.tip{
    text-align: center;
}
</style>



