<template>
    <div class="detail">
        <div class="detail-content">
                <div class="detail-info">
                    <h4 v-text="bookList[index].title"></h4>
                    <p v-text="bookList[index].date"></p>
                    <span v-text="'来源:'+bookList[index].author_name"></span>
                    <div class="detail-img">
                    <img :src="bookList[index].thumbnail_pic_s02">
                    </div>
                    <p class="nm">{{bookList[index].nm}}</p><br />
                    <p class="nm1">{{bookList[index].nm1}}</p><br />
                    <p class="nm2">{{bookList[index].nm2}}</p><br />
                    <p class="nm3">{{bookList[index].nm3}}</p>
                </div>
                <div class="detail-img1">
                    <img :src="bookList[index].thumbnail_pic_s03" >
                </div>
        </div>
    </div>
</template>

<script>
import Axios from 'axios'
export default {
    data(){
        return{
            bookList : [],
            index:-100
        }
    },
mounted() {
    
    let url2 = '/static/data/bookdetail.json'
    Axios.get(url2).then(res=>{
        this.index = this.$route.params.id;
        this.bookList = res.data.result.data
        console.log(this.bookList)
    }).catch();
},
}
</script>

<style scoped>
.detail{
    margin:1rem auto;
    padding: 0.2rem;
}
.detail-info p{
    float: left;
}
.detail-info span{
    float: left;
    margin-left: 0.5rem;
}
.detail-img img{
    display: block;
    margin: 0 auto;
    padding-top: 0.3rem;
}
.detail-info .nm{
    text-indent: 2em;
    margin-top: 0.3rem;
}
.detail-info .nm1{
    text-indent: 2em;
    margin-top: 0.3rem;

}
.detail-info .nm2{
    text-indent: 2em;
margin-top: 0.3rem;
}
.detail-info .nm3{
    text-indent: 2em;
    margin-top: 0.3rem;
}
.detail-img1 img{
    display: block;
    margin: 0 auto;
    padding-top: 0.2rem;
}
</style>



