<template>
    <div class="vdo">
        <ul class="vdo-content">
            <li v-for="(video,index) in videoList" :key="index" class="veo">
                <div class="video-img">
                    <!-- <img :src="video.img"> -->
                    <p>{{video.title}}</p>
                </div>
                <div class="container">
                    <video :src="video.play" controls width="100%"></video>
                </div>
                
            </li>
        </ul>
    </div>
</template>

<script>
import Axios from "axios"
export default {
    data(){
        return{
            videoList:[],
        }
    },
    methods:{
        loadingDate(){
            let url = '/static/data/hotdata.json';
            Axios.get(url) .then((res)=>{
                this.videoList=res.data.data;
                console.log(this.videoList);
            console.log(res);
            })
            .catch()
        }
    },
    mounted(){
        this.loadingDate();
    }
}
</script>

<style scoped>
.vdo{
    margin-top: 1rem;
    margin-bottom:1rem;
}
.video-img{
 margin:0.1rem 0.2rem;
}
</style>



