<template>
    <div class="detail">
        <ul class="detail-content">
            <li v-for="item in movieList" :key="item.movieId">
            <div class="detail-img">
                <img :src="item.pic_url" />
            </div>
            <div class="detail-info">
                <h2 v-text="item.movieName"></h2>
                <h3 v-text="item.writers"></h3>
                <p v-text="item.xianqing.rating +'分'"></p>
                <p>{{item.xianqing.genres}}</p>
                <p>{{item.xianqing.language}}</p>
                <p>{{item.xianqing.writers}}</p>
                <p>{{item.xianqing.film_locations}} / {{item.xianqing.runtime}}</p>
            </div>
            
        <hr />
        <div>
            <h3>主演</h3>
            <p>{{item.xianqing.actors}}</p>
        </div>
        <div>
            <h3>简介</h3>
            <p>{{item.xianqing.plot_simple}}</p>
        </div>
        </li>
        </ul>
    </div>
</template>

<script>
import Axios from 'axios'
export default {
    data(){
        return{
            movieList:[],
        }
    },
    mounted() {
        let url2  = '/static/data/moviedata1'+this.$route.params.movieId+'.json'
        Axios.get(url2).then(res =>{
        //    console.log(res);
            this.movieList = res.data.result;
            console.log(this.movieList);
        }).catch();
    },
}
</script>

<style scoped>
.detail{
    margin: 1rem 0 ;
    padding: 0.2rem;
}
.detail-content{

    margin-bottom: 0.2rem;
}
.detail-img{
    margin: 0 auto;
}
.detail-img img{
    display: block;
    margin:0 auto;
}
.detail-info{
text-align: center;
}
.detail h3{
    text-align: center;
}
</style>



