<template>
    <div class="ell">
        <ul class="book-list">
            <li @click="goDetail(index)" v-for="(book,index) in booklist" :key="index" class="book">
            <div class="book-img"><img :src="book.thumbnail_pic_s"></div>
            <div class="book-info">
                <p>{{book.title}}</p>
                <p>来源:{{book.author_name}}</p>
            </div>
            </li>
            <div class="loading" v-show="loadingShow"><img src="@/assets/img/loading.gif"></div>
        </ul>
        <div class="tip" v-show="tip">
            <p>没有更多了</p>
        </div>
    </div>
</template>

<script>
import Axios from "axios"
export default {
    data(){
        return{
            booklist:[],
            loadingShow:false,
            tip:false,

        }
    },
methods:{
    loadingData(){
        let url2='/static/data/bookdata.json';
        Axios.get(url2)
        .then((res)=>{
            //console.log(res);
            let limit = res.data.result.data;
            /* console.log(limit); */
            let data = limit.slice(
                this.booklist.length,
                this.booklist.length + 7
            );
            /* console.log(this.booklist,this.booklist.length); */
            if(data.length<7){
                this.tip  = true;
            }
            this.booklist = this.booklist.concat(data);
        })
        .catch(()=>{
            alert('cuowu')
        })
    },
    goDetail(id){
        this.$router.push({name:'BookDetail',params:{id:id}});

    }
},
mounted() {
    this.loadingData();
    window.onscroll = () =>{
        let clientHeight = document.documentElement.clientHeight;
        let scrollTop = Math.ceil(document.documentElement.scrollTop || document.body.scrollTop);
        let scrollHeight = document.documentElement.scrollHeight;
        if( scrollTop + clientHeight >= scrollHeight){
        this.loadingShow = true;
        if(!this.tip){
            this.loadingData();
        }else{
            this.loadingShow = false;
        }
        console.log('这里是世界尽头:'+this.tip,this.loadingShow)
            }
    }
},
};
</script>

<style scoped>
.ell{
    margin-top: 1rem;
        margin-bottom: 1rem;
}
.ell .book-list{
    width: 100%;

}
.book{
        display:flex;
    padding: 0.2rem;
    border-bottom:1px solid #ccc; 
}
.book-img{
    width: 40%;
    flex: 1 1 0;

    margin-right: 0.2rem;
}
.book-img img{
    width:100%;

} 
.book-info{
    width: 50%; 
    flex: 2 1 0;
}
.book-info p:first-child{
    font-weight:bold;
    color:#000;
    margin-bottom: 0.2rem
}

.book-info p:last-child{
    color: #ccc;
}
.loading{
    margin-bottom: 1rem;
    text-align: center;
}

.tip{
    text-align:center;
}
</style>



