<template>
    <div class="header" :style="{background:bg}">
        <span class="home" @click="gohome">首页</span>
        <h2 class="title">
            <slot name="title">头条</slot>
        </h2>
    </div>
</template>

<script>
export default {
    methods:{
        gohome(){
            this.$router.push('/');
        },
    },
    props:['bg']
}
</script>

<style scoped>
.header{
    width: 100%;
    height: 1rem;
    background: #FE3A3A;
    position: fixed;
    top: 0;
    line-height: 1rem;
}
.home{
    color:#fff;
    margin-left: 0.2rem;
}
.title{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    font-size: 0.5rem;
    font-weight: bold;
}
</style>



